import React, { Component, Fragment } from 'react';
import BadgesOutput from './BadgesOutput';

//sets up the legend of the page that takes in user input for filtering
class ATFilters extends Component {

  render() {

    //get the current state of each badge from the main constructor using object deconstructing
    const { TypeOfActionCat, IntersectionalityCat, SubtopicCat, RecAudienceCat } = this.props.passingBadges;

    //set up arrays to capture all unique badges to then set them up as "buttons" in the legend
    //split action tags into "learning" and "doing categories" depending on the formuliac "ActionCategory tab"
    const LearnActionTags = [];
    const ActActionTags = [];
    const IntersectionalityTags = [];
    const SubtopicTags = [];
    const RecAudienceTags = [];
    this.props.airtableData.map(card => {

      card.fields.TypeOfAction.map(element => {
        if (LearnActionTags.indexOf(element) === -1 && ActActionTags.indexOf(element) === -1) {
          //special logic to split into two categories
          if(card.fields.ActionCategory.toLowerCase() === "learn") {
            LearnActionTags.push(element)
          } else {
            ActActionTags.push(element)
          }
        }
      });

      if (card.fields.Intersectionality) {
        card.fields.Intersectionality.map(element => {
          if (IntersectionalityTags.indexOf(element) === -1) {
            IntersectionalityTags.push(element)
          }
        });
      }

      if (card.fields.Subtopic) {
        card.fields.Subtopic.map(element => {
          if (SubtopicTags.indexOf(element) === -1) {
            SubtopicTags.push(element)
          }
        });
      }

      if (card.fields.RecAudience) {
        card.fields.RecAudience.map(element => {
          if (RecAudienceTags.indexOf(element) === -1) {
            RecAudienceTags.push(element)
          }
        });
      }
    });

    return (
      <Fragment>
        <div className="row">
          <div className="table-responsive">
            <table className="table table-bordered pill-matrix table-filters th-lg">
              <tbody>
                <tr>
                  <th scope="row" className="text-nowrap align-middle">
                    How do you want to <span className="strong">learn</span>?
                  </th>
                  <td id="filter-TypeOfAction">
                    <BadgesOutput BadgesData={LearnActionTags} LegendCheck={true} CatID={TypeOfActionCat} CatName={"TypeOfActionCat"} clickAction={this.props.clickAction} />
                  </td>
                </tr>
                <tr>
                  <th scope="row" className="text-nowrap align-middle">
                    What do you want to <span className="strong">do</span>?
                  </th>
                  <td id="filter-TypeOfAction">
                    <BadgesOutput BadgesData={ActActionTags} LegendCheck={true} CatID={TypeOfActionCat} CatName={"TypeOfActionCat"} clickAction={this.props.clickAction} />
                  </td>
                </tr>
                <tr>
                  <th scope="row" className="text-nowrap align-middle">
                    What intersections are you curious about?
                  </th>
                  <td id="filter-Intersectionality">
                    <BadgesOutput BadgesData={IntersectionalityTags} LegendCheck={true} CatID={IntersectionalityCat} CatName={"IntersectionalityCat"} clickAction={this.props.clickAction} />
                  </td>
                </tr>
                <tr>
                  <th scope="row" className="align-middle">
                    What other issues are important you?
                  </th>
                  <td id="filter-Subtopic">
                    <BadgesOutput BadgesData={SubtopicTags} LegendCheck={true} CatID={SubtopicCat} CatName={"SubtopicCat"} clickAction={this.props.clickAction} />
                  </td>
                </tr>
                <tr>
                  <th scope="row" className="align-middle">
                    Who are you looking to educate?
                  </th>
                  <td id="filter-RecAudience">
                    <BadgesOutput BadgesData={RecAudienceTags} LegendCheck={true} CatID={RecAudienceCat} CatName={"RecAudienceCat"} clickAction={this.props.clickAction} />
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </ Fragment>
    );

  }
}

export default ATFilters;