import React, {Component, Fragment} from 'react';
import uuid from 'react-uuid';

//reads in string array of badges from AT and displays custom-designed pills using programatically named classes from index.css
class BadgesOutput extends Component {
    render() {
        //first check if null
        if(this.props.BadgesData) {
            if(this.props.LegendCheck) {
                return this.props.BadgesData.map((badge) => (
                <Fragment key={uuid()}>
                    {/*CSSify the badge name, for coloring and state for selecting and then display the badge */}
                    <span
                    data-name={badge}
                    onClick={e => this.props.clickAction(e, this.props.CatName)}
                    className={"badge badge-pill " + ((this.props.CatID[badge]) ? "legend-button-active" : "legend-button-inactive") + " text-wrap " + badge.toLowerCase().split(" ").join("-").replace("+","plus")}>
                        {badge}
                    </span>
                    {" "}
                </Fragment>
                ));
            } else {
                // Badges for individual cards 
                return this.props.BadgesData.map((badge) => (
                    <span key={uuid()} className={"badge badge-pill text-wrap " + badge.toLowerCase().split(" ").join("-").replace("+","plus")}>{badge}</span>
                ));
            }
        } else {
            return null;
        }
    }
}

export default BadgesOutput;