import React, { Component } from 'react';
import homePage from './Home';
import { HashRouter, Switch, Route } from 'react-router-dom';
import Navigation from './NavBar';
import listPage from './fullList.js';
import submitPage from './submitEntry';
// import { render } from '@testing-library/react'; for testing

class App extends Component {
  render() {
    return (
      //Navbar
      <HashRouter>
        <Navigation />

        <Switch>
          <Route exact path="/" component={homePage} />
          <Route path="/fullList" component={listPage} />
          <Route path="/submitEntry" component={submitPage} />
        </Switch>

      </HashRouter>
    )
  }
}

export default App;